<template>
  <v-card width="100%">
    <v-data-table
      :headers="headers"
      :items="data"
      :items-per-page="Number(-1)"
      hide-default-footer
      class="table"
      @click:row="handleRowClick"
    >
      <template v-slot:[`item.time`]="{ item }">
        {{ format(item.time) }}
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
const headers = [
  { text: '', value: 'semester' },
  { text: '', value: 'name' },
  { text: '', value: 'teacher' },
  { text: '', value: 'department' },
  { text: '', value: 'time' },
  { text: '', value: 'location' },
  { text: '', value: 'credit' },
  { text: '', value: 'tags' },
  // { text: '', value: 'comment' },
  // { text: '', value: 'like' },
]

export default {
  name: 'CourseResult',

  props: {
    data: {
      type: Array,
      required: true,
    },
  },

  data: () => ({
    headers,
  }),

  methods: {
    handleRowClick(course) {
      let routeData = this.$router.resolve({ name: 'course', params: { id: course.id } })
      window.open(routeData.href, '_blank')
    },
    format(t) {
      t = t.replace(/'/g, '"')
      t = JSON.parse(t)
      let s = ''
      let f = true
      const ch = { 1: '', 2: '', 3: '', 4: '', 5: '' }
      for (let i of ['1', '2', '3', '4', '5', '6']) {
        if (t[i].length > 0) {
          if (!f) s += ''
          f = false
          s += ch[i]
          s += ' '
          s += t[i].join(',')
        }
      }
      return s
    },
  },
}
</script>

<style scoped>
.table >>> tbody tr :hover {
  cursor: pointer;
}
</style>
